module.exports = "body {\n  margin: 0;\n  font-family: Arial; }\n\n.topnav {\n  overflow: hidden;\n  background: white;\n  background: linear-gradient(0deg, rgba(255, 255, 255, 0) 0%, #272727 100%);\n  text-align: center;\n  height: 3.6em; }\n\n.topnav a {\n  display: inline-flex;\n  color: #fff;\n  text-align: center;\n  padding: 14px 16px;\n  text-decoration: none;\n  font-size: 17px; }\n\n.active {\n  background-color: #4CAF50;\n  color: white; }\n\n.topnav .icon {\n  display: none; }\n\n.dropdown {\n  float: left;\n  overflow: hidden; }\n\n.dropdown .dropbtn {\n  font-size: 17px;\n  border: none;\n  outline: none;\n  color: white;\n  padding: 14px 16px;\n  background-color: inherit;\n  font-family: inherit;\n  margin: 0;\n  transition: 0.3s all ease-in-out; }\n\n.dropdown-content {\n  display: none;\n  position: absolute;\n  background-color: #f9f9f9;\n  min-width: 160px;\n  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\n  z-index: 1; }\n\n.dropdown-content a {\n  float: none;\n  color: grey;\n  padding: 12px 16px;\n  text-decoration: none;\n  display: block;\n  text-align: left; }\n\n.topnav a:hover, .dropdown:hover .dropbtn {\n  border-bottom: 5px solid #CC9F08;\n  color: #CC9F08; }\n\n.dropdown-content a:hover {\n  background-color: #ddd;\n  color: black; }\n\n.dropdown:hover .dropdown-content {\n  display: block; }\n\n@media screen and (max-width: 600px) {\n  .topnav a:not(:first-child), .dropdown .dropbtn {\n    display: none; }\n  .topnav a.icon {\n    float: right;\n    display: block; } }\n\n@media screen and (max-width: 600px) {\n  .topnav.responsive {\n    position: relative; }\n  .topnav.responsive .icon {\n    position: absolute;\n    right: 0;\n    top: 0; }\n  .topnav.responsive a {\n    float: none;\n    display: block;\n    text-align: left; }\n  .topnav.responsive .dropdown {\n    float: none; }\n  .topnav.responsive .dropdown-content {\n    position: relative; }\n  .topnav.responsive .dropdown .dropbtn {\n    display: block;\n    width: 100%;\n    text-align: left; } }\n\n/*# sourceMappingURL=data:application/json;base64, */"